@import "../bootstrap5/functions";
@import "../bootstrap5/variables"; 
@import "../bootstrap5/mixins";
@import "../variables";
@import "../mixins";


.c-view-reminder{
    display: none;
    position: fixed;
    top:0;
    left:0;
    right: 0;
    bottom:0;
    margin:0;
    width: 100vw;
    height: 100vh;
    z-index: 9999999;
    pointer-events: none;
    &.c-view-reminder--show{
        display: block;
    }
}
.c-view-reminder-wrap{
    display:flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: 100vw;
    height: 100vh;
    position: relative;
}
.c-view-reminder-phone {
    height: 50px;
    width: 100px;
    border: 3px solid white;
    border-radius: 10px;
    animation: rotate 1.5s ease-in-out infinite alternate;
}
  
.c-view-reminder-message {
    color: #fff;
    font-size: 1em;
    margin-top: 40px;
}
.c-view-reminder-overlay{
    position: fixed;
    top:0;
    left:0;
    right: 0;
    bottom:0;
    margin:0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0,0,0,.8);
}
@keyframes rotate {
0% {
        transform: rotate(0deg)
    }
    50% {
        transform: rotate(-90deg)
    }
    100% {
        transform: rotate(-90deg)
    }
}
